.first-screen {
  background-color: $grey-background;
  position: relative;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 746px;
  background-image: url(../img/hero-desktop.png);
  background-size: 1150px 746px;
  position: relative;
  top: -59px;
}

.first-screen__caption {
  text-align: center;
  font-weight: 700;
  font-size: 53px;
  padding-top: 175px;
}

.first-screen__blocks {
  display: flex;
  background-repeat: no-repeat;
  padding-top: 330px;
  margin: 0 auto;
  width: 1050px;
  &::after {
    content: "";
    background-image: url("../img/zigzag-fill.svg");
    width: 100%;
    height: 34px;
    z-index: 0;
    position: absolute;
  }
}

.first-screen__block {
  display: inline-block;
  color: $basic-white;
  font-weight: 700;
  font-size: 20px;
  font-family: $OpenSans;
  line-height: 30px;
  min-width: 525px;
  padding: 25px 350px 34px 37px;

  &--first {
    background-color: $light-green;
  }
  &--second {
    background-color: $green;
  }
}

.first-screen__svg {
  position: absolute;
  bottom: -6px;
  z-index: 9;
  top: auto;
  width: 100%;
}
