.busket {
  margin-top: 55px;
  position: relative;

  &::after {
    content: "";
    width: 1050px;
    height: 23px;
    background-image: url("../img/zigzag-line.svg");
    background-repeat: no-repeat;
    z-index: 2;
    display: inline-block;
    position: absolute;
    bottom: 0;
  }
}

.busket-background {
  background-image: url(../img/busket-desktop.png);
  background-repeat: no-repeat;
  background-size: 535px 636px;
  width: 1050px;
  min-height: 636px;
  background-position: top right;
}

.busket__flex {
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-end;
  padding-left: 46px;
}

.busket__signature {
  font-size: 17px;
  line-height: 17px;
  color: $light-green;
  text-transform: uppercase;
  padding-right: 43px;
  padding-top: 45px;
}

.busket__caption {
  font-size: 53px;
  font-weight: bold;
}

.busket__card {
  width: 292px;
  margin-left: 175px;
  font-family: $OpenSans;
  font-size: 17px;
  line-height: 30px;
  padding-bottom: 73px;
}

.busket__p {
  padding-bottom: 60px;
  margin: 0;
}

.busket__table {
  border-collapse: collapse;
}

.busket__td {
  padding-bottom: 15px;
  &--color {
    padding-right: 60px;
  }
  &--diameter {
    padding-right: 28px;
  }

  &--height {
    padding-right: 40px;
  }
}

.busket__tr {
  border-bottom: 1px solid $line-grey;
}

.busket__span {
  color: $light-black;
  font-size: 20px;
  font-weight: 700;
  display: block;
  text-align: center;
  padding-top: 60px;
  padding-bottom: 33px;
}

.busket__button {
  padding: 16px 86px;
  border: 2px solid $light-black;
  display: block;
  align-self: center;
  text-align: center;
}
