.reviews {
  background-image: url(../img/review.png);
  background-repeat: no-repeat;
  background-position: top left;
  min-height: 457px;
  padding-top: 80px;
  width: 1050px;
  font-family: $OpenSans;
  font-size: 17px;
  padding-left: 170px;
  display: flex;
  flex-wrap: wrap;
}

.reviews__wrapper {
  position: relative;

  padding-bottom: 60px;
  &::after {
    margin-left: 50px;
    content: "";
    width: 1050px;
    height: 23px;
    background-image: url(../img/zigzag-line.svg);
    z-index: 2;
    display: inline-block;
    position: absolute;
    bottom: 0;
  }
}

.reviews__caption {
  margin: 0;
  font-family: $OpenSans;
  font-size: 53px;
  font-weight: 700;
  padding-right: 480px;
}

.reviews__p {
  margin: 0 auto;
  display: block;
  font-size: 17px;
  line-height: 30px;
  font-family: $OpenSans;
  display: block;
}

.reviews__name {
  font-weight: 700;
  line-height: 24px;
  display: block;
  padding-bottom: 10px;
}

.reviews__write {
  margin-left: -100px;
}

.reviews__wrap {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.review__link {
  &--back {
    &::after {
      content: "";
      width: 50px;
      height: 26px;
      display: inline-block;
      background: url("../img/arrow-back.svg");
    }
  }

  &--forward {
    &::after {
      content: "";
      width: 50px;
      height: 26px;
      display: inline-block;
      background: url("../img/arrow-forward.svg");
    }
  }
}

.reviews__links {
  padding-right: 20px;
}
