.about-us {
  padding-top: 80px;
}

.about-us__about-us {
  margin: 0;
  font-size: 53px;
  font-weight: 700;
  font-family: $OpenSans;
  padding-left: 175px;
}

.about-us__list {
  display: flex;
  flex-wrap: wrap;
  padding-left: 175px;
  padding-bottom: 90px;
}

.about-us__item {
  font-size: 20px;
  line-height: 22px;
  margin-right: 115px;
  font-weight: 700;
  padding-top: 110px;
  background-repeat: no-repeat;
  background-position: 0 60px;

  &--material {
    background-image: url(../img/icon-1.svg);
  }

  &--price {
    background-image: url(../img/icon-2.svg);
  }

  &--like {
    background-image: url(../img/icon-3.svg);
  }

  &--hand-made {
    background-image: url(../img/icon-4.svg);
  }

  &--support {
    background-image: url(../img/icon-5.svg);
  }

  &--gift {
    background-image: url(../img/icon-6.svg);
  }
}

.about-us__list ul {
  padding-left: 0;
  margin: 0;
}
