.contacts-iframe {
  display: flex;
  width: 1050px;
  margin: 0 auto;
  padding-top: 70px;
  padding-bottom: 105px;
}

.contacts {
  padding-left: 175px;
  padding-right: 60px;
  position: relative;

  &::before {
    content: "";
    width: 47px;
    height: 81px;
    background-image: url(../img/flag.svg);
    background-repeat: no-repeat;
    z-index: 2;
    position: absolute;
    left: 0;
    display: inline-block;
  }
}

.contacts__caption {
  font-family: $OpenSans;
  font-weight: 700;
  font-size: 53px;
  color: $basic-black;
}

.contacts__td {
  font-size: 17px;
  font-weight: 400;
  min-width: 80px;
  margin-bottom: 33px;

  &--first {
    padding-right: 20px;
  }
}

.contacts__list {
  display: flex;
  flex-direction: row;
  padding-left: 0;
  margin: 0;
  padding-bottom: 30px;

  &--last {
    margin-bottom: 85px;
  }
}

.contacts__item {
  margin-right: 14px;

  &--email {
    padding-bottom: 2px;
    border-bottom: 1px solid $light-green-form;
  }
}
