.header {
  color: $basic-white;
}

.main-nav__wrap {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  z-index: 2;

  @media (max-width: $mobile) {
    flex-direction: column;
  }

  @media (max-width: $tablet) {
    .main-nav__list-children:nth-child(2) {
      order: 1;
    }

    .main-nav__list-children:nth-child(3) {
      order: 2;
    }

    .main-nav__list-children:nth-child(1) {
      order: 3;
    }

    .main-nav__list-children:nth-child(5) {
      order: 4;
    }
  }

  .main-nav__list-children {
    @media (max-width: $mobile) {
      display: flex;
      margin: 0 auto;
      align-items: center;
      flex-direction: column;

      .main-nav__list-children:nth-child(2) {
        order: 1;
      }

      .main-nav__list-children:nth-child(3) {
        order: 3;
      }

      .main-nav__list-children:nth-child(1) {
        order: 2;
      }

      .main-nav__list-children:nth-child(5) {
        display: none;
      }
    }

    .main-nav__img {
      width: 149px;
      height: 72px;
    }
  }

  .main-nav__list-children {
    padding-left: 0;
    margin: 0;
    padding-right: 35px;

    &--last {
      padding-right: 0;
    }

    &--search {
      display: flex;
      flex-direction: row;
    }
  }

  .main-nav__line {
    padding-left: 0px;
    display: flex;
    margin: 0;

    &--notpadding {
      padding-right: 0;
    }
  }

  .main-nav__toggle {
    display: none;
  }
}

.main-nav__link {
  color: $basic-black;
  font-weight: 700;

  &--big {
    font-style: 17px;

    @media (max-width: $tablet) {
      font-size: 15px;
    }

    @media (max-width: $mobile) {
      font-size: 17px;
    }
  }

  &--small {
    font-size: 15px;

    @media (max-width: $tablet) {
      display: none;
    }
  }

  &--span {
    font-weight: 400;
    font-family: "Open Sans";
    padding-top: 20px;
  }
}

.main-nav__item {
  display: block;
  padding-top: 33px;
  padding-bottom: 20px;

  &--border {
    border-right: 1px solid $black-opacity1;
    padding-right: 32px;
    font-weight: 700;
    font-size: 17px;
  }

  &--noborder {
    padding-left: 35px;
  }

  &--basket {
    text-align: end;
    width: 100%;
    border-left: 1px solid $black-opacity1;
    white-space: nowrap;
    padding-left: 70px;
  }

  &--product {
    padding-right: 60px;
    padding-top: 20px;
  }
}

